import getMath from './mathExpression';
import trackScroll from './scrollTo';


const getCalculation = (branchs) => {
    console.log(branchs); ///////////////
    const answerSection = document.querySelector('.answer');

    let parameters = [],
    U = 0,
    I = [],
    textArr = [],
    answer;
    class branchElements {
        constructor(
            numberBranch, nameR, resistance, nameG, 
            conductance, nameE, voltage, ...elements
            ) {
            this.numberBranch = numberBranch;
            this.conductance = conductance;
            this.nameG = nameG;
            this.resistance = resistance;
            this.nameR = nameR;
            this.voltage = voltage;
            this.nameE = nameE;
            this.elements = elements;
        }
    }

        


    function getParametrsBranch(branchs) {
        let numberBranch = 0;

        //–ü–µ—Ä–µ–±–æ—Ä –≤–µ—Ç–≤–µ–π
        branchs.forEach((branch, i) => {
            if (i < 2){
                return;
            }

            let R = [],
            K = 0,
            E =  [],
            N = 0;

            //–ü–µ—Ä–µ–±–æ—Ä —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≤–µ—Ç–≤–∏
            branch.elements.forEach(block => {

                //–ü—Ä–∞–≤–∏–ª—å–Ω–∞—è –Ω—É–º–µ—Ä–∞—Ü–∏—è
                branchs[0].elements.forEach((knot, number) => {
                    if(knot.y === block.y){
                        numberBranch = number + 1;
                        Arrow(knot, numberBranch);
                    }
                });
                
                if(block.type === 0){
                    K = K + 1;
                    R[K] = block;
                }

                if(block.type === 1){
                    N = N + 1;
                    E[N] = block;
                }

            });
        
            if(numberBranch === 1){
                textArr.push(
                    `–¢–∞–∫ –∫–∞–∫ –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω—ã–µ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è 
                    —Ç–æ–∫–æ–≤ –¥–æ —Ä–∞—Å—á—ë—Ç–∞ —Ü–µ–ø–∏ –Ω–∞–º –Ω–µ–∏–∑–≤–µ—Å—Ç–Ω—ã ‚Äî –ø—Ä–æ–∏–∑–≤–æ–ª—å–Ω–æ 
                    —É–∫–∞–∑—ã–≤–∞–µ–º –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è —Ç–æ–∫–æ–≤ –≤ –≤–µ—Ç–≤—è—Ö.`
                );
                getAnswerBlock(textArr);
                textArr = [];
            }

            GetValueResist(R, K, numberBranch);
            GetValueVoltage(E, N, numberBranch);
        });
    }


    function getAnswerBlock(textArr) {
        
        answer = document.createElement('div');
        answer.classList.add('answer__block');
        answerSection.append(answer);
        textArr.forEach(element => {
            getMath(element, answer);
        });
    }



    function GetValueResist(R, K, numberBranch){
        parameters[numberBranch] = new branchElements(numberBranch);

        if(K < 2){

            parameters[numberBranch].nameG = `g${R[K].number}`;
            parameters[numberBranch].conductance = 1 / +R[K].resistance;
            parameters[numberBranch].nameR = `r${R[K].number}`;
            parameters[numberBranch].resistance = +R[K].resistance;

            let resistance = +R[K].resistance;
            let answer = toFixed3(1 / +R[K].resistance);
            
            textArr.push(`–ù–∞–π–¥—ë–º –ø—Ä–æ–≤–æ–¥–∏–º–æ—Å—Ç—å –≤–µ—Ç–≤–∏ ‚Ññ${numberBranch}`);
            textArr.push(`tac g${R[K].number} =~1/r${R[K].number}`);
            textArr.push(`tac g${R[K].number} =~1/${resistance}~=`+
            ` ${answer} –°–º`);
            getAnswerBlock(textArr);
            textArr = [];




        } else {

            let Name = '',
            value = 0,
            sum = 0,
            expressionValue = '',
            expression = '';

            R.forEach(resist => {
                Name = `${Name}` + `${resist.number}`;
                expression += `+ R${resist.number} `;
                expressionValue += `+ ${resist.resistance} `;
                sum = +resist.resistance;
                value = value + sum;
                
            });

            
            expression = `${expression}`.slice(2);
            expressionValue = `${expressionValue}`.slice(2);

            parameters[numberBranch].nameR = `r${Name}`;
            parameters[numberBranch].resistance = +value;

            value = 1 / +value;

            parameters[numberBranch].nameG = `g${Name}`;
            parameters[numberBranch].conductance = +value;


            //–û–∫—Ä—É–≥–ª—è–µ–º —á–∏—Å–ª–æ –¥–ª—è –≤—ã–≤–æ–¥–∞
            value = toFixed3(+value);
            
            
            textArr.push(`–ù–∞–π–¥—ë–º –ø—Ä–æ–≤–æ–¥–∏–º–æ—Å—Ç—å –≤–µ—Ç–≤–∏ ‚Ññ${numberBranch}`);
            textArr.push(`tac g${Name} =~1/${expression}`);
            textArr.push(`tac g${Name} =~1/${expressionValue}~= ${value} –°–º`);
            getAnswerBlock(textArr);
            textArr = [];

        }
    }

    function GetValueVoltage(R, K, numberBranch){
        let voltage = 0;
        let Name = '';

        if(K === 0){

/*             textArr.push(`–≠–¥—Å –¥–ª—è –≤–µ—Ç–≤–∏ ‚Ññ${numberBranch}`);
            textArr.push(`0 –í`);
            getAnswerBlock(textArr);
            textArr = []; */


            parameters[numberBranch].nameE = ` + 0`;
            parameters[numberBranch].voltage = 0;
            return;

        }
        if(K < 2){

            voltage = +(R[K].voltage);
            if(R[K].rotate == 2){
                voltage = -voltage;
            }
                
/*             textArr.push(`–≠–¥—Å –¥–ª—è –≤–µ—Ç–≤–∏ ‚Ññ${numberBranch}`);
            textArr.push(`E${R[K].number} = ${voltage} –í`);
            getAnswerBlock(textArr);
            textArr = []; */

            Name = ` + E${R[K].number}`;
            if(R[K].rotate == 2){
                Name = ` - E${R[K].number}`;
            }

            parameters[numberBranch].nameE = `${Name}`;
            parameters[numberBranch].voltage = +voltage;

        } else {

            let value = 0,
            expressionValue = '',
            expression = '';

            Name = '';
            
            R.forEach(EDS => {
                let EDSname = ' + E',
                plus = '+';
                

                Name = `${Name}` + `${EDS.number}`;

                voltage = +EDS.voltage;
                console.log(EDS.rotate);
                if(EDS.rotate == 2){
                    voltage = -voltage;
                    EDSname = ' - E';
                    plus = '-';
                }

                expression += `${EDSname}${EDS.number} `;
                expressionValue += `${plus} ${EDS.voltage} `;

                value = value + voltage;
            });

                expression = SliceElement(`${expression}`, '+ ');
                expressionValue = SliceElement(`${expressionValue}`, '+ ');


            textArr.push(`–≠–î–° –¥–ª—è –≤–µ—Ç–≤–∏ ‚Ññ${numberBranch}`);
            textArr.push(`tac E${Name} = ${expression}`);
            textArr.push(`tac E${Name} = ${expressionValue} = ${value} –í`);
            getAnswerBlock(textArr);
            textArr = [];

            
            if(value < 0){  
                Name = ` - E${Name}`;
            } else{
                Name = ` + E${Name}`;
            }
            parameters[numberBranch].nameE = `${Name}`;
            parameters[numberBranch].voltage = +value;
        }
    }

    function Arrow(block, numberBranch, revers = 0){

            // –£–¥–∞–ª—è–µ–º –≤—Å–µ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –±–ª–æ–∫–æ–≤(–Ω–∞–∑–≤–∞–Ω–∏–µ —Ç–æ–∫–æ–≤)
            while (block.element.firstChild) {
                block.element.removeChild(block.element.firstChild);
            }

            // –ó–∞–Ω–æ–≤–æ –¥–æ–±–∞–≤–ª—è–µ–º –Ω–∞–∑–≤–∞–Ω–∏–µ —É–∑–ª–∞
            if(numberBranch === 2){ 
                const spanText = document.createElement('span');
                spanText.classList.add('knot-text');
                spanText.textContent = `A`;
                block.element.append(spanText);
            }

            const span = document.createElement('span');
            span.classList.add('top');
            span.textContent = `I${numberBranch}`;
            block.element.prepend(span);
            if(!revers){
                block.element.style.cssText = 
                `background: url(../svg/Arrow.SVG) -130% -90% no-repeat;
                background-size: 97px;`;
            } else {
                console.log('revers');
                block.element.style.cssText = 
                `background: url(../svg/Arrow.SVG) -130% -90% no-repeat;
                background-size: 97px;`;
            }
    }



    function toFixed3(num){ 
        let x = 0;
        for (let n = 5; n >= 0; --n) {
            num = +num;
            num = num.toFixed(n);
            x = num.length;
            x = x - 1;

            //–ò—Å–ø—Ä–∞–≤–ª—è–µ–º –æ—à–∏–±–∫—É —Å –æ–∫—Ä—É–≥–ª–µ–Ω–∏–µ–º 5
            if(n > 3){
                if(num.slice(-1) == '5'){
                    num = num.slice(0, x) + `6`;
                }
                continue;
            }

            if(num.slice(-1) == '0'){
                num = num.slice(0, x);
                if(num.slice(-1) == '0'){
                    continue;
                } else {
                    num = +num;
                    return num;
                }
            } else {
                num = +num;
                return num;
            }
        }
    }



    function SliceElement(str, element){
        if(str.substr(0, 2) == element){
            return str.slice(2);
        } else{
            console.log(str);
            return str;
        }
    } 

    

    function GetVoltageS—Åheme() {
        let sumEG = 0,
        sumG = 0,
        expressionEG = '',
        expressionG = '',
        a = '',
        b = '',
        plus = '+';

    parameters.forEach(element => {

        sumEG = sumEG + element.conductance * element.voltage;

        if(element.voltage < 0) { 
            plus = '';
        }else{
            plus = ' + ';
        }

        expressionEG = expressionEG + 
        `${plus} ${element.voltage}` +  
        `‚ãÖ${ toFixed3(element.conductance) }`;

        a = a + `${element.nameE}` +  `‚ãÖ${element.nameG}`;

        sumG = sumG + element.conductance;
        expressionG = expressionG + ` + ${ toFixed3(element.conductance) }`;
        b = b + ` + ${element.nameG}`;

    });

        U = sumEG / sumG;

        a = SliceElement(a, ' +');
        b = SliceElement(b, ' +');
        expressionG = SliceElement(expressionG, ' +');
        expressionEG = SliceElement(expressionEG, ' +');
        
        textArr.push(`–ù–∞–ø—Ä—è–∂–µ–Ω–∏–µ –º–µ–∂–¥—É —É–∑–ª–∞–º–∏ –ê-–í —Ä–∞–≤–Ω–æ:`);
        textArr.push(`tac Uab =~${a}/${b}~=~${expressionEG}/${expressionG}~= `+
        `${toFixed3(U)} –í`);
        textArr.push(
        `notice –≠–î–° –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–Ω–∞—è –∫ —É–∑–ª—É A, –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç—Å—è —Å–æ –∑–Ω–∞–∫–æ–º ¬´+¬ª, 
        –µ—Å–ª–∏ –≤ –ø—Ä–æ—Ç–∏–≤–æ–ø–æ–ª–æ–∂–Ω—É—é —Å—Ç–æ—Ä–æ–Ω—É, —Ç–æ —Å–æ –∑–Ω–∞–∫–æ–º ¬´-¬ª.`
        );
        getAnswerBlock(textArr);
        textArr = [];
    }
    



    function FindCurrent() {
        let sumEU = 0,
        sumG = 0,
        expressionEU = '',
        expressionG = '',
        a = '',
        b = '',
        plus = '+'; 
        parameters.forEach((element, i) => {
            I[i] = (element.voltage - U) * element.conductance;
            I[i] = toFixed3(I[i]);
            
            //–ú–µ–Ω—è–µ–º –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Ç–æ–∫–∞ –Ω–∞ —Å—Ö–µ–º–µ –ø—Ä–∏ –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏—è—Ö
/*             if(I[i] < 0) {
                Arrow(branchs[0].elements[i-1], i, 1);
            } */

            if(U < 0) { 
                plus = ' +';
            }else{
                plus = ' -';
            }
    
            expressionEU =  `${element.voltage}` + 
            `${plus} ${Math.abs(toFixed3(U))}`;

            a = `${element.nameE}` + ` - U`;
            b = `${element.nameG}`;

            a = SliceElement(a, ' +');

        textArr.push(`–ù–∞–π–¥—ë–º —Ç–æ–∫ –≤ –≤–µ—Ç–≤–∏ ‚Ññ${i}`);
        textArr.push(`tac I${i} = ${a}‚ãÖ${b} = `+
        `(${expressionEU})‚ãÖ${toFixed3(element.conductance)} = ${I[i]} –ê`);
        if(I[i] < 0) {
            textArr.push(
            `notice –¢–∞–∫ –∫–∞–∫ —Ç–æ–∫ I${i} –ø–æ–ª—É—á–∏–ª—Å—è —Å –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã–º 
            –∑–Ω–∞—á–µ–Ω–∏–µ–º - —Ä–µ–∞–ª—å–Ω–æ–µ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Ç–æ–∫–∞ –≤ —Ü–µ–ø–∏, –±—É–¥–µ—Ç –æ—Ç —É–∑–ª–∞
             A –∫ —É–∑–ª—É B, —Ç–æ –µ—Å—Ç—å –ø—Ä–æ—Ç–∏–≤–æ–ø–æ–ª–æ–∂–Ω–æ –∏–∑–Ω–∞—á–∞–ª—å–Ω–æ –ø—Ä–∏–Ω—è—Ç–æ–º—É.`
            );
        }
        getAnswerBlock(textArr);
        textArr = [];
        });
    }


    function getBalance(parameters){
        let sumEI = 0,
        sumRII = 0,
        expressionEI = '',
        expressionRII = '',
        a = '',
        b = '',
        plus = '+';

        textArr.push(
            `–î–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –ø—Ä–∞–≤–∏–ª—å–Ω–æ—Å—Ç–∏ —Ä–µ—à–µ–Ω–∏—è —Å–æ—Å—Ç–∞–≤–∏–º 
            <a href="https://electrikam.com/balans` +
            `-moshhnostej-v-cepi-postoyannogo-toka/">
            –±–∞–ª–∞–Ω—Å –º–æ—â–Ω–æ—Å—Ç–µ–π</a>.`
        );

        parameters.forEach((element, i) => {

            
            sumEI = sumEI + element.voltage * I[i];
            sumRII = sumRII + (I[i] * I[i]) * element.resistance;
        
            
            // –¥–æ–±–∞–≤–ª—è–µ–º —Å–∫–æ–±–∫–∏ –∫ –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã–º —Ç–æ–∫–∞–º
/*             if(I[i] < 0) { 
                I[i] = `(${I[i]})`;
            } */


            //–ï—Å–ª–∏ –Ω–∞–ø—Ä–≤–ª–µ–Ω–∏–µ —Ç–æ–∫–æ–≤ –Ω–µ —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ–º –≠–î–°
            if((element.voltage * I[i]) < 0){

                textArr.push(
                    `notice –ò—Å—Ç–æ—á–Ω–∏–∫ ${SliceElement(element.nameE, ' -')} –ø–æ—Ç—Ä–µ–±–ª—è–µ—Ç 
                    —ç–ª–µ–∫—Ç—Ä–∏—á–µ—Å–∫—É—é —ç–Ω–µ—Ä–≥–∏—é, —Ç.–∫. –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≠–î–° –Ω–µ —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å 
                    –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ–º —Ç–æ–∫–∞ –≤ –≤–µ—Ç–≤—è—Ö.
                `);
                textArr.push(
                    `notice –°–ª–µ–¥–æ–≤–∞—Ç–µ–ª—å–Ω–æ, –≤ –±–∞–ª–∞–Ω—Å –º–æ—â–Ω–æ—Å—Ç–µ–π –≠–î–° 
                    ${SliceElement(element.nameE, ' -')} 
                    –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç—Å—è —Å–æ –∑–Ω–∞–∫–æ–º –º–∏–Ω—É—Å.
                `);
                
                if(element.voltage > 0){
                    element.voltage = element.voltage * -1; 
                    element.nameE = SliceElement(element.nameE, ' +');
                    element.nameE = ` - ${element.nameE}`;
                }
                
            } else {      

                if(element.voltage < 0){
                    element.voltage = element.voltage * -1;
                    element.nameE = SliceElement(element.nameE, ' -');
                    element.nameE = ` + ${element.nameE}`;
                }
            }

            if(I[i] < 0) { 
                I[i] = I[i] * -1;
            }

            if(element.voltage < 0){
                plus = '';
            } else {
                plus = '+';
            }

            a = a + `${element.nameE} ‚ãÖ I${i}`;
            expressionEI = expressionEI + 
            ` ${plus}${element.voltage} ‚ãÖ ${I[i]}`;

            b = b + ` + I${i}<sup>2</sup> ‚ãÖ ${element.nameR}`;
            expressionRII = expressionRII + 
            ` + ${I[i]}<sup>2</sup> ‚ãÖ ${element.resistance}`;
        });

        a = SliceElement(a, ' +');
        b = SliceElement(b, ' +');

        expressionEI = SliceElement(expressionEI, ` +`);
        expressionRII = SliceElement(expressionRII, ' +');

        textArr.push(`tac ${a} = ${b}`);
        textArr.push(`tac ${expressionEI} = ${expressionRII}`);
        textArr.push(`tac ${toFixed3(sumEI)} –í—Ç = ${toFixed3(sumRII)} –í—Ç`);
        if(Math.abs(((sumEI - sumRII)/ sumEI)) < 0.03){
            textArr.push(`tac –ë–∞–ª–∞–Ω—Å —Å–æ—à–µ–ª—Å—è.`);
        }else{
            textArr.push(`–û–®–ò–ë–ö–ê.`);
        }
        getAnswerBlock(textArr);
        textArr = [];
    }
 

    getParametrsBranch (branchs);
    GetVoltageS—Åheme();
    FindCurrent();
    getBalance(parameters);
    trackScroll(document.documentElement.clientHeight);



};




export default getCalculation;